.main {
  border: 2px solid #f5f6fb;
  border-radius: 20px 20px 0px 0px;
  background-color: #f5f6fb;
}
.scroll_table__body {
  height: 344px;
  overflow-x: auto;
  margin-top: 0px;
  margin-bottom: 20px;
  border: 2px solid #f5f6fb;
  @media screen and (min-width: 768px) {
    height: 346px;
  }
}

.scroll_table table {
  width: 100%;
  table-layout: fixed;
  border: none;
}
.scroll_table thead th {
  font-weight: bold;
  text-align: left;
  border: none;
  padding: 10px 15px;
  font-size: 12px;
}
.scroll_table tbody td {
  text-align: left;
  padding: 5px 15px;
  font-size: 12px;
  // vertical-align: top;
  border-bottom: 2px solid #f4f6fb;
}

/* Стили для скролла */
::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  background: #f5f6fb;
}
::-webkit-scrollbar-thumb {
  background-color: #ff751d;
  border-radius: 2px;
}
